<template>
  <div class="container">
    <a href="" class="button is-primary">Primary</a>
    <div class="block">
      {{msg}}
    </div>
  </div>



</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: 'test'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



